.game-start{display:flex;align-items:center;width:100%;height:100vh}.game-start_bg{background:linear-gradient(to top left, #fff3eb 50%, #fff 50%)}.game-start__wrapper{display:flex;align-items:center;justify-content:space-between;width:110rem;margin:0 auto}.game-start__logo{width:100%;max-width:45rem}.game-start__content{display:flex;flex-direction:column}.game-start__title{font-family:Inter;font-size:5.6rem;font-style:normal;font-weight:600;line-height:6.5rem;max-width:52.5rem;margin-top:0;margin-bottom:6.5rem}.game-start__score-title{font-family:Inter;font-size:3.2rem;font-style:normal;font-weight:600;line-height:3.7rem;color:#97979b;margin:0}.game-start__button{font-family:Inter;font-size:2rem;font-style:normal;font-weight:600;line-height:2.3rem;text-align:center;color:#ffffff;background-color:#ff8b37;border:none;border-radius:1.2rem;padding:2rem 2.4rem;transition:all 0.3s;width:29.6rem}.game-start__button:hover{background-color:#ffac70}.game-start__button:active{background-color:#e87928}@media screen and (max-width: 1439px) and (min-width: 992px){.game-start__wrapper{width:80rem}}@media screen and (max-width: 991px){.game-start__wrapper{flex-direction:column;justify-content:space-between;height:100%;padding:5rem 1.5rem}.game-start__logo{max-width:19.6rem;flex:50% 1}.game-start__content{align-items:center;justify-content:space-between;flex:50% 1}.game-start__title{font-size:3.2rem;line-height:3.712rem;text-align:center}}@media screen and (max-width: 767px){.game-start__button{font-size:1.4rem;line-height:1.6rem;border-radius:0.8rem;padding:1.6rem 2.4rem;width:28.8rem}}

