;padding:2.6rem 3.3rem;background-image:url(../../static/media/answer-inactive.157d72db.svg);text-align:left;position:relative;border:none;background-color:transparent;transition:all 0.3s}.answer-button__letter{color:#ff8b37;margin-right:1rem}.answer-button:before,.answer-button:after{content:"";display:block;width:1.7rem;top:calc(50% - 0.1rem);position:absolute;height:0.1rem;background-color:#d0d0d8}.answer-button:before{left:-1.7rem}.answer-button:after{right:-1.7rem}.answer-button:hover{background-image:url(../../static/media/answer-hover.226e9422.svg)}.answer-button:hover:before,.answer-button:hover:after{background-color:#ff8b37}.answer-button_selected,.answer-button_selected:hover{background-image:url(../../static/media/answer-selected.7e5aaa8b.svg)}.answer-button_selected:before,.answer-button_selected:after,.answer-button_selected:hover:before,.answer-button_selected:hover:after{background-color:#ff8b37}.answer-button_correct,.answer-button_correct:hover{background-image:url(../../static/media/answer-correct.29122387.svg)}.answer-button_correct:before,.answer-button_correct:after,.answer-button_correct:hover:before,.answer-button_correct:hover:after,.answer-button_correct:hover:before,.answer-button_correct:hover:after,.answer-button_correct:hover:hover:before,.answer-button_correct:hover:hover:after{background-color:#47d867}.answer-button_wrong,.answer-button_wrong:hover{background-image:url(../../static/media/answer-wrong.9948cae0.svg)}.answer-button_wrong:before,.answer-button_wrong:after,.answer-button_wrong:hover:before,.answer-button_wrong:hover:after,.answer-button_wrong:hover:before,.answer-button_wrong:hover:after,.answer-button_wrong:hover:hover:before,.answer-button_wrong:hover:hover:after{background-color:#ec6259}@media screen and (max-width: 1439px) and (min-width: 992px){.game-field__answer{width:26rem}.answer-button{padding:1.35rem 3.3rem}}@media screen and (max-width: 991px){.game-field__question{flex:0 0 100%;padding:13.6rem 0 2.4rem;align-i