.game-field {
  background: #e5e5e5;
  height: 100vh;
  
  &__wrapper {
    display: flex;
    justify-content: space-between;
    height: 100%;
  }

  &__question {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex: 0 0 75%;
    padding: 10.5rem 8rem 8.5rem;
  }

  &__title {
    font-family: Inter;
    font-size: 3.2rem;
    font-style: normal;
    font-weight: 600;
    line-height: 3.7rem;
    max-width: 70rem;
  }

  &__answers {
    display: flex;
    flex-wrap: wrap;
    max-width: 84rem;
  }

  &__answer {
    width: 39rem;
    margin: 0 1.5rem 3.5rem;
  }

  &__steps {
    display: flex;
    background: #ffffff;
    flex: 0 0 25%;
    overflow-x: hidden;
    padding: 6.4rem 0 3.2rem;
    justify-content: flex-start;
  }

  &__steps-list {
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background: #ffffff;
    overflow-x: hidden;
  }

  &__step {
    font-family: Inter;
    font-size: 2rem;
    font-style: normal;
    font-weight: 400;
    line-height: 2.3rem;
    text-align: center;
    width: 24rem;
    padding: 0.9rem 1rem 0.8rem;
    position: relative;
    background-size: cover;

    &:not(:last-child) {
      margin-bottom: 0.8rem;
    }

    &:before,
    &:after {
      content: "";
      display: block;
      width: 100%;
      top: calc(50% - 0.1rem);
      position: absolute;
      height: 0.1rem;
      background-color: #d0d0d8;
    }

    &:before {
      left: -100%;
    }

    &:after {
      right: -100%;
    }

    &_inactive {
      background-image: url(../../images/step-inactive.svg);
      color: #1C1C21;

      &:before,
      &:after {
        background-color: #d0d0d8;
      }
    }
    
    &_current {
      background-image: url(../../images/step-current.svg);
      color: #ff8b37;

      &:before,
      &:after {
        background-color: #ff8b37;
      }
    }

    &_finished {
      background-image: url(../../images/step-finished.svg);
      color: #d0d0d8;

      &:before,
      &:after {
        background-color: #d0d0d8;
      }
    }
  }
}

.answer-button {
  font-family: Inter;
  font-size: 2rem;
  font-style: normal;
  font-weight: 400;
  line-height: 2.3rem;
  background-size: cover;
  padding: 2.6rem 3.3rem;
  background-image: url(../../images/answer-inactive.svg);
  text-align: left;
  position: relative;
  border: none;
  background-color: transparent;
  transition: all 0.3s;

  &__letter {
    color: #ff8b37;
    margin-right: 1rem;
  }

  &:before,
  &:after {
    content: "";
    display: block;
    width: 1.7rem;
    top: calc(50% - 0.1rem);
    position: absolute;
    height: 0.1rem;
    background-color: #d0d0d8;
  }

  &:before {
    left: -1.7rem;
  }

  &:after {
    right: -1.7rem;
  }

  &:hover {
    background-image: url(../../images/answer-hover.svg);
  }

  &:hover:before,
  &:hover:after {
    background-color: #ff8b37;
  }

  &_selected,
  &_selected:hover {
    background-image: url(../../images/answer-selected.svg);

    &:before,
    &:after {
      background-color: #ff8b37;
    }
  }

  &_correct,
  &_correct:hover {
    background-image: url(../../images/answer-correct.svg);

    &:before,
    &:after,
    &:hover:before,
    &:hover:after {
      background-color: #47d867;
    }
  }

  &_wrong,
  &_wrong:hover {
    background-image: url(../../images/answer-wrong.svg);

    &:before,
    &:after,
    &:hover:before,
    &:hover:after {
      background-color: #ec6259;
    }
  }
}

@media screen and (max-width: 1439px) and (min-width: 992px) {
  .game-field {
    &__answer {
      width: 26rem;
    }
  }

  .answer-button {
    padding: 1.35rem 3.3rem;
  }
}

@media screen and (max-width: 991px) {
  .game-field {
    &__question {
      flex: 0 0 100%;
      padding: 13.6rem 0 2.4rem;
      align-items: center;
    }

    &__title {
      text-align: center;
    }

    &__answers {
      justify-content: center;
    }

    &__answer {
      width: 28.8rem;
      margin: 0 1.5rem 0.8rem;
    }

    &__steps {
      position: absolute;
      width: 100%;
    }
  }

  .answer-button {
    padding: 1.65rem 3.3rem;
  }

  .mobile-hidden {
    display: none;
  }
}


@media screen and (max-width: 767px) {
  .game-field {
    &__title {
      font-size: 1.8rem;
      line-height: 2.088rem;
      max-width: 28.8rem;
    }

    &__step {
      padding: 0.4rem 1rem 0.5rem;
      
      &_inactive {
        background-image: url(../../images/step-inactive-mobile.svg);
      }
      
      &_current {
        background-image: url(../../images/step-current-mobile.svg);
      }

      &_finished {
        background-image: url(../../images/step-finished-mobile.svg);
      }
    }
  }
}
